import { Box, Container, Typography } from "@mui/material";
import React from "react";
import Typewritter from 'typewriter-effect';



function TitlePanel(props: any) {
    return (
        <Box display="flex" alignItems="center" justifyContent="space-between" paddingY="140px" bgcolor="#003580">
            <Container>
                <Typography variant="h3" component="h2" fontWeight="bold" color={'white'}>
                    Danh sch
                    <span>
                        <Typewritter onInit={(typewritter) => {
                            typewritter.typeString(`danh mc cc ${props?.title}`).pauseFor(2000).deleteAll().typeString(`danh mc cc ${props?.title}`).start();
                        }} />
                    </span>
                </Typography>
                {/* <p className={classes.text}>ng nhp vo ti khon admin  c th chnh sa d liu</p>
                <Button size="large" sx={{ paddingY: "15px" }} variant="contained">NG NHP / NG K</Button> */}
            </Container>
        </Box>
    )
}

export default React.memo(TitlePanel);